import React, { Component } from 'react'
import { LockOutlined, UserOutlined } from '@ant-design/icons';
import { Input, Button } from 'antd';
import http from '../../config/http'
import { Link } from 'react-router-dom'

import './login.styl'

interface ILoginState {
  account: string
  password: string
}

interface IRouter {
  history: any
}

export class login extends Component<IRouter, ILoginState> {

  constructor(props: any) {
    super(props);
    this.state = {
      account: '',
      password: '',
    }
  }

  onChange = (key: keyof ILoginState, e: any) => {
    const newState = {} as ILoginState
    newState[key] = e.target.value
    this.setState(newState)
  }

  goToSignUp = () => {
    this.props.history.push('/signUp')
  }
  goToIndex = () => {
    this.props.history.push('/')
  }

  submit = async () => {
    const { account, password } = this.state
    try {
      await http.post('/sign_in/user', { account, password })
      this.goToIndex()
    } catch (e) {
      throw new Error(e)
    }
  }

  render() {
    return (
      <div className="page-login">
        <h1 className="login-header"></h1>
        <Input
          className="account-input"
          placeholder="" allowClear
          prefix={<UserOutlined style={{ color: 'rgba(0,0,0,.25)' }} />}
          onChange={e => {this.onChange('account', e)}}
          style={{marginBottom: '1em'}}
        />
        <Input.Password
          className="password-input"
          placeholder="" allowClear
          prefix={<LockOutlined style={{ color: 'rgba(0,0,0,.25)' }} />}
          onChange={e => {this.onChange('password', e)}}
        />
        <Button
          className="login-btn"
          type="primary" onClick={this.submit}>
            
        </Button>
        <p className="register-link">
          <Link to="/signUp"></Link>
        </p>
      </div>
    );
  }
}

export default login
